// import three.js library from suitable location
import * as THREE from 'three'

// this script will be used only when the parameter " ?views=8 " is added at the end of the URL
// e.g. https://dummysite.com/index.html?views=8

var IglooRenderer = function ( camera, renderer, scene, objectToFollow) {

    // 8 perspective cameras
    var camera0;
    var camera1;
    var camera2;
    var camera3;
    var camera4;
    var camera5;
    var camera6;
    var camera7;

    // look for viewports parameter in URL
    const urlString = window.location.search;
    const urlParams = new URLSearchParams(urlString);
    const viewportNum = urlParams.get('views');

    // objects from main scene
    this.camera = camera;
    this.renderer = renderer;
    this.scene = scene;
    //this.container = container;

    // object for igloo camera to follow, default is camera object
    //var follow_object = null; //if nothing to follow/static camera;
    var follow_object = objectToFollow;

    // pixel ratio
    var pixel_ratio = renderer.getPixelRatio();

    // calculate size of faces/viewports
	var view_width = ( this.renderer.domElement.width / 8 ) / pixel_ratio; 
	var view_height = ( this.renderer.domElement.height ) / pixel_ratio;

    var fov = 45;
    var aspect = 1;
    var near = this.camera.near;
    var far = this.camera.far;

    var pointerX = 0;
    var pointerY = 0;
    var activeCamera = null;

    // imagine the camera is rotated clockwise following a compass' cardinal & ordinal directions,
    // starting from South which is the view behind the default camera view

    // viewing Back of camera (South)
    camera0 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera0.lookAt( 0, 0, 1 );

    // viewing 45 degrees between Back and Left of camera (South-West)
    camera1 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera1.lookAt( -0.5, 0, 0.5 );

    // viewing Left of camera (West)
    camera2 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera2.lookAt( -1 , 0, 0);

    // viewing 45 degrees between Front and Left of camera (North-West)
    camera3 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera3.lookAt( -0.5, 0, -0.5 );

    // viewing Front of camera (North)
    camera4 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera4.lookAt( 0, 0, -1 );

    // viewing 45 degrees between Front and Right of camera (North-East)
    camera5 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera5.lookAt( 0.5, 0, -0.5 );

    // viewing Right of camera (East)
    camera6 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera6.lookAt( 1, 0, 0 );

    // viewing 45 degrees between Back and Right of camera (South-East)
    camera7 = new THREE.PerspectiveCamera( fov, aspect, near, far );
    camera7.lookAt( 0.5, 0, 0.5 );

    
    camera0.position.copy(camera.position);
    camera1.position.copy(camera.position);
    camera2.position.copy(camera.position);
    camera3.position.copy(camera.position);
    camera4.position.copy(camera.position);
    camera5.position.copy(camera.position);
    camera6.position.copy(camera.position);
    camera7.position.copy(camera.position);

    window.addEventListener( 'pointerdown', function ( event ) {

        var clientx = event.clientX % view_width;
        pointerX = (clientx / view_width) * 2 - 1;
        pointerY = - (event.clientY / view_height) * 2 + 1;

        var view_number = Math.floor(event.clientX / view_width);
        switch (view_number) {
            case 0: activeCamera = camera0; break;
            case 1: activeCamera = camera1; break;
            case 2: activeCamera = camera2; break;
            case 3: activeCamera = camera3; break;
            case 4: activeCamera = camera4; break;
            case 5: activeCamera = camera5; break;
            case 6: activeCamera = camera6; break;
            case 7: activeCamera = camera7;
        }
    })

    this.update = function() {
        pixel_ratio = renderer.getPixelRatio();
        view_width = ( this.renderer.domElement.width / 8) / pixel_ratio; 
        view_height = ( this.renderer.domElement.height ) / pixel_ratio; 
        near = this.camera.near;
        far = this.camera.far;
    }

    this.enabled = function() {
        if (viewportNum == 8) {
            return true;
        }
        else {
            return false;
        }
    }

    this.follow = function(object) {
        follow_object = object;
    }

    this.pointerDownCoords = function() {
        return [pointerX, pointerY];
    }

    this.activeCamera = function() {
        return activeCamera;
    }

    this.render = function() {

        if (viewportNum == 8) {

            // Renderer Viewport approach:
            // draw the faces using Scissors and Viewport methods
            // in WebGLRenderer.

            if (follow_object != null) {
                camera0.position.copy(follow_object.position);
                camera1.position.copy(follow_object.position);
                camera2.position.copy(follow_object.position);
                camera3.position.copy(follow_object.position);
                camera4.position.copy(follow_object.position);
                camera5.position.copy(follow_object.position);
                camera6.position.copy(follow_object.position);
                camera7.position.copy(follow_object.position);
            }
            this.renderer.setScissorTest(true);
            this.renderer.setScissor(0, 0, view_width, view_height);
            this.renderer.setViewport(0, 0, view_width, view_height);
            this.renderer.render( this.scene, camera0 );

            this.renderer.setScissor(view_width, 0, view_width, view_height);
            this.renderer.setViewport(view_width, 0, view_width, view_height);
            this.renderer.render( this.scene, camera1 );

            this.renderer.setScissor(view_width * 2, 0, view_width, view_height);
            this.renderer.setViewport(view_width * 2, 0, view_width, view_height);
            this.renderer.render( this.scene, camera2 );

            this.renderer.setScissor(view_width *  3, 0, view_width, view_height);
            this.renderer.setViewport(view_width * 3, 0, view_width, view_height);
            this.renderer.render( this.scene, camera3 );

            this.renderer.setScissor(view_width * 4, 0, view_width, view_height);
            this.renderer.setViewport(view_width * 4, 0, view_width, view_height);
            this.renderer.render( this.scene, camera4 );

            this.renderer.setScissor(view_width * 5, 0, view_width, view_height);
            this.renderer.setViewport(view_width * 5, 0, view_width, view_height);
            this.renderer.render( this.scene, camera5 );

            this.renderer.setScissor(view_width * 6, 0, view_width, view_height);
            this.renderer.setViewport(view_width * 6, 0, view_width, view_height);
            this.renderer.render( this.scene, camera6 );

            this.renderer.setScissor(view_width * 7, 0, view_width, view_height);
            this.renderer.setViewport(view_width * 7, 0, view_width, view_height);
            this.renderer.render( this.scene, camera7 );

            this.renderer.setScissorTest(false);

            return true;
        }
        else {
            return false;
        }
    }
}

export { IglooRenderer };