import './style.css'
import * as THREE from 'three'
//import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { gsap } from 'gsap'
import { IglooRenderer } from './IglooRendererModified.js'


/**
 * This project is made by Stephanie V Budiman for NUS Libraries
 * It is specifically setup to work with 360imx using IglooRendererModified script
 * 
 * It consists of a 3D scene imported from Sketchup
 * and a set of locations that the camera can follow
 */

/**
 * Base
 */

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
scene.background = new THREE.Color( '#87CEFA' )
scene.fog = new THREE.Fog( '#ffffff', 0.1, 1500 )


/**
 * Audio
 * Original gamelan music by Singa Nglaras
 */

 const listener = new THREE.AudioListener()
 const audioLoader = new THREE.AudioLoader()
 const bgMusic = new THREE.Audio( listener )
 
 //Background music
 audioLoader.load( '/music/mijil-dhempel.ogg', function( buffer) {
     bgMusic.setBuffer( buffer )
     bgMusic.setVolume( 0.1 )
     bgMusic.setLoop(true)
     bgMusic.play()
 })

/**
 * Sizes
 */
 const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

/**
 * Set the coordinates that will be used for the camera
 * Assign a blank 3D object to move in between these locations
 * The camera's position will be set to always follow this blank 3D object
 */
const loc0 = new THREE.Vector3(225,30,50)
const loc1 = new THREE.Vector3(-20,30,50)
const loc2 = new THREE.Vector3(-20,30,-100)
const loc3 = new THREE.Vector3(-20,30,-300)
const loc4 = new THREE.Vector3(-20,30,-420)
const loc5 = new THREE.Vector3(70,30,-420)
const loc6 = new THREE.Vector3(290,30,-420)

const locations = [loc0, loc1, loc2, loc3, loc4, loc5, loc6]

var currectLocIndex = 0
const maxLocLength = locations.length - 1

const objectToFollow = new THREE.Object3D
objectToFollow.position.copy(locations[currectLocIndex])
scene.add( objectToFollow )
 
const objectPosition = new THREE.Vector3()
objectToFollow.getWorldPosition(objectPosition) 

/**
 * Camera
 */
// Base camera

const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 1000)
camera.position.copy(locations[currectLocIndex])

// Controls
// there's no need for orbit controls in 360imx
/*
const controls = new OrbitControls(camera, canvas)
controls.target.set(objectPosition.x,30,objectPosition.z)
//controls.enablePan = false
*/

/**
 * Renderer
 */
 const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding

var igloo = new IglooRenderer(camera, renderer, scene, objectToFollow)


window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

    igloo.update()
})

/**
 * Lights
 */

 const ambientLight = new THREE.AmbientLight(0xffffff, 0.6)
 scene.add(ambientLight)
 
 const directionalLight = new THREE.DirectionalLight(0xffffff, 0.6)
 directionalLight.position.set(-5, 5, 0)
 scene.add(directionalLight)
 
 const hemisphereLight = new THREE.HemisphereLight( 0xffffbb, 0x080820, 0.8 )
 hemisphereLight.position.set(0,10,0)
 scene.add( hemisphereLight )
 

/**
 * Models
 */

var loader = new GLTFLoader()

const dracoLoader = new DRACOLoader()
dracoLoader.setDecoderPath('/draco/')
loader.setDRACOLoader(dracoLoader)

const models = ['court','seat1','seat2','gate','ground']
const path = '/models/gg03/'
const fileType = '.glb'

for (let i=0; i < models.length; i++)
{
    loader.load(
        path+models[i]+fileType,
        (gltf) =>
        {
            gltf.scene.scale.set(0.2,0.2,0.2)
            scene.add(gltf.scene)
        }
    )
}

//kanchil models are loaded individually so we can hide them alternately

let kanchil2 = new THREE.Object3D()
loader.load(
    '/models/gg03/kanchil2.glb',
    (gltf) =>
    {
        kanchil2 = gltf.scene
        kanchil2.scale.set(0.2,0.2,0.2)
        scene.add(kanchil2)
    }
)
//kanchil2.visible = false //to hide the kanchil


let kanchil3 = new THREE.Object3D()
loader.load(
    '/models/gg03/kanchil3.glb',
    (gltf) =>
    {
        kanchil3 = gltf.scene
        kanchil3.scale.set(0.2,0.2,0.2)
        scene.add(kanchil3)
    }
)

let kanchil4 = new THREE.Object3D()
loader.load(
    '/models/gg03/kanchil4.glb',
    (gltf) =>
    {
        kanchil4 = gltf.scene
        kanchil4.scale.set(0.2,0.2,0.2)
        scene.add(kanchil4)
    }
)

/**
 * Menus
 * aka information that will pop up when users visit a certain location and press the PageUp button
 */

const menu0 = new THREE.Mesh(
    new THREE.PlaneGeometry(15,12),
    new THREE.MeshBasicMaterial(
        {map: new THREE.TextureLoader().load('/textures/gg3-court.png'), transparent: true, fog: false}
    )
)
menu0.position.set(loc0.x,loc0.y,loc0.z-20)

const menu1 = new THREE.Mesh(
    new THREE.PlaneGeometry(15,12),
    new THREE.MeshBasicMaterial(
        {map: new THREE.TextureLoader().load('/textures/gg3-seat1.png'), transparent: true, fog: false}
    )
)
menu1.position.set(loc2.x-20,loc2.y,loc2.z)
menu1.rotation.y = THREE.Math.degToRad(90)

const menu2 = new THREE.Mesh(
    new THREE.PlaneGeometry(15,12),
    new THREE.MeshBasicMaterial(
        {map: new THREE.TextureLoader().load('/textures/gg3-seat2.png'), transparent: true, fog: false}
    )
)
menu2.position.set(loc3.x-20,loc3.y,loc3.z)
menu2.rotation.y = THREE.Math.degToRad(90)

const menu3 = new THREE.Mesh(
    new THREE.PlaneGeometry(15,12),
    new THREE.MeshBasicMaterial(
        {map: new THREE.TextureLoader().load('/textures/gg3-seat3.png'), transparent: true, fog: false}
    )
)
menu3.position.set(loc5.x,loc5.y,loc5.z-20)

const menu4 = new THREE.Mesh(
    new THREE.PlaneGeometry(15,12),
    new THREE.MeshBasicMaterial(
        {map: new THREE.TextureLoader().load('/textures/gg3-seat4.png'), transparent: true, fog: false}
    )
)
menu4.position.set(loc6.x,loc6.y,loc6.z-20)

scene.add(menu0,menu1,menu2,menu3,menu4)

const menus = [menu0, menu1, menu2, menu3, menu4]

//all menus are set invisible by default except for the first one at the beginning of the scene
for (let m of menus) {
    m.visible = false
}
menus[currectLocIndex].visible = true


/**
 * Clues
 */
var cluesIndex = 0
const clueImgLen = 5
 
const cluesTexture1 = new THREE.TextureLoader().load('/textures/gg3shuffles/00.png')
const cluesTexture2 = new THREE.TextureLoader().load('/textures/gg3shuffles/01.jpg')
const cluesTexture3 = new THREE.TextureLoader().load('/textures/gg3shuffles/02.jpg')
const cluesTexture4 = new THREE.TextureLoader().load('/textures/gg3shuffles/03.png')
const cluesTexture5 = new THREE.TextureLoader().load('/textures/gg3shuffles/04.jpg')
 
const cluesTextures = [cluesTexture1, cluesTexture2, cluesTexture3, cluesTexture4, cluesTexture5]
 
const clues = new THREE.Mesh(
    new THREE.PlaneGeometry(2,2),
    new THREE.MeshBasicMaterial(
        {map: cluesTextures[cluesIndex], fog: false}
    )
)
clues.position.set(loc0.x,loc0.y-0.6,loc0.z-14.9) 
scene.add(clues)


/**
 * Right & Wrong answers
 */
const wrongAnswer = new THREE.TextureLoader().load('/textures/gg3-wrong.png')
const correctAnswer = new THREE.TextureLoader().load('/textures/gg3-correct.png')

const answer = new THREE.Mesh(
    new THREE.PlaneGeometry(16,16),
    new THREE.MeshBasicMaterial(
        {map: wrongAnswer, fog: false}
    )
)
answer.position.set(loc0.x,loc0.y,loc0.z-14.8) 
answer.visible = false
scene.add(answer)

var answerIsCorrect = false

 /**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

var menuIsUp = true
document.addEventListener( 'keydown', onKeyDown )

//const experiment = document.getElementById("experiment")
//experiment.innerHTML = spotsNames[currentMarkIndex]

const tick = () =>
{
    //const elapsedTime = clock.getElapsedTime()
    //const deltaTime = elapsedTime - previousTime
    //previousTime = elapsedTime

    if (menus[0].visible === true) {
        clues.visible = true
    } else {
        clues.visible = false
    }


    if (answerIsCorrect) {
        //set a bit of delay time before proceeding to the next scene
        //so that people can read the information first
        //but it's not needed in 360imx due to internet latency

        //const timeOut = setTimeout(delay, 1000)
        location.replace("https://panji360.github.io/gg_04")
    }
    
    // Update controls
    //controls.update()
    
    // Render
    //renderer.render(scene, camera)

    if (igloo.render() != true) {
        renderer.render( scene, camera )
    }

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()

/* 
function delay() {
    location.replace("https://panji360.github.io/gg_04")
}
*/


function onKeyDown( event ) {
    
    switch (event.keyCode)
    {
        case 33: //pageUp key
            if (!menuIsUp) {
                switch (currectLocIndex) {
                    //not every location has information, so there's no need to call for a menu
                    case 0: menus[0].visible = true; menuIsUp = true; break
                    case 2: menus[1].visible = true; menuIsUp = true; break
                    case 3: menus[2].visible = true; menuIsUp = true; break
                    case 5: menus[3].visible = true; menuIsUp = true; break
                    case 6: menus[4].visible = true; menuIsUp = true; break
                }
            }

        break

        case 34: //pageDown key
            if (menuIsUp) {
                switch (currectLocIndex) {
                    case 0: menus[0].visible = false; menuIsUp = false; break
                    case 2: menus[1].visible = false; menuIsUp = false; break
                    case 3: menus[2].visible = false; menuIsUp = false; break
                    case 5: menus[3].visible = false; menuIsUp = false; break
                    case 6: menus[4].visible = false; menuIsUp = false; break
                }
                answer.visible = false
            }

        break

        case 37: //left key
            for (let m of menus) {
                m.visible = false
                menuIsUp = false
            }
            answer.visible = false
            if (currectLocIndex === 0) {
                currectLocIndex = maxLocLength
            } else {
                currectLocIndex--
            }
            gsap.to(camera.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 3 })
            gsap.to(objectToFollow.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 3 })

        break


        case 39: //right key
            for (let m of menus) {
                m.visible = false
                menuIsUp = false
            }
            answer.visible = false

            if (currectLocIndex === maxLocLength) {
                currectLocIndex = 0
            } else {
                currectLocIndex++ 
            }
            gsap.to(camera.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 3 })
            gsap.to(objectToFollow.position, { x: locations[currectLocIndex].x, z: locations[currectLocIndex].z ,ease: "circle", duration: 3 })

        break

        case 38: //up key
            if (clues.visible === true) {
                if (cluesIndex === 0) {
                    cluesIndex = clueImgLen
                } else {
                    cluesIndex--
                }
                clues.material.map = cluesTextures[cluesIndex]
            }
            
        break

        case 40: //down key
            if (clues.visible === true) {
                if (cluesIndex === clueImgLen) {
                    cluesIndex = 0
                } else {
                    cluesIndex++
                }
                clues.material.map = cluesTextures[cluesIndex]
            }
        break

        case 13: // enter key
            if (clues.visible === true) {
                if (cluesIndex === 3) {
                    answer.material.map = correctAnswer
                    answerIsCorrect = true
                } else {
                    answer.material.map = wrongAnswer
                    answerIsCorrect = false
                }
                answer.visible = true
            } 
        break

    }
}